import request from "../../common/util.js"
export default {
	components: {

	},
	data() {
		return {
			wentiDetail:[]
		}
	},
	methods: {
		gotoDetail(i){
		let url = "/pages/question/question?id="+i;
			uni.navigateTo({
				url: url
			})
		},
	},
	// onLoad相当于created生命周期
	onLoad(op) {
		console.log(op)
		this.req.request("/searchTitle",{
			 "title":op.id
		},'POST',(res) =>{
			console.log(res)
			this.wentiDetail = res
			
		},function(e) {
			uni.showToast({
				title: e.errMsg,
				mask: true,
				duration: 1200,
				icon: 'none'
			});
		});
	}

}
